  import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
import { uniSwiperDot } from "@dcloudio/uni-ui";
  import {IndexModel} from "../../model";
  const  indexModel = new IndexModel()
export default {
  name:'index',
  props:{
    list:{
      type:Object
    },
    activities:{
      type:Object
    },
    activities_title:{
      type:Object
    },
    shop:{
      type:Object
    },
  },
  data() {
    return {
      discount:false,
      current: 0,
      mode: "long",
      dotsStyles: {
        backgroundColor: "rgba(253, 208, 0, 0.3)",
        border: "1px rgba(253, 208, 0, 0.3) solid",
        color: "#fff",
        selectedBackgroundColor: "rgba(253, 208, 0, 0.7)",
        selectedBorder: "1px rgba(253, 208, 0, 0.7) solid"
      },
      
    };
  },
  methods: {
    // 调用接口
    _getIndex(){
      indexModel.getIndex().then((res) => {
        if(res.data) {
          this.list = res.data
        }
      })
    },
    // 关闭打折
    close_discount(){
      this.discount = false
    },
    //icon跳转
    icon_url(url){
      uni.navigateTo({
        url
      });
    },
    // 美容包年
    active(url){
      if(url == "") {
        this.discount = true
      } else {
        uni.navigateTo({
          url
        });
      }

    },
    // 购买车
    car_shop(url){
      console.log(url)
      uni.navigateTo({
        url
      });
    },
    change(e) {
      this.current = e.detail.current;
    },
    park_emergency() {
      uni.navigateTo({
        url: "/pages/park_emergency/park_emergency"
      });
    },
    cosmetology() {
      uni.navigateTo({
        url: "/pages/cosmetology/cosmetology"
      });
    },
    changeShop()
    {
      uni.navigateTo({
        url:`/pagesB/choice_store/choice_store`
      })
    }
  },
  components: {
    uniSwiperDot,
    uniNavBar
  },
  // onLoad: function() {
  //   console.log('onLoadindex')
  //   this._getIndex()
  // },
  // onShow: function() {
  //   uni.hideTabBar();
  // },
  // onHide: function() {},
  mounted(){
    
  },
  watch:{
    list(val){
      console.log(val)
    },
    activities(val){
      console.log(val)
    }
  }
};
